body, html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color: #101118;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  background-color: #101118;
  background-repeat: repeat;
}

.website{
  background: #34e89e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#34e89e), to(#0f3443));
  background: linear-gradient(to bottom, #34e89e, #0f3443); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.particles canvas {
  display: block;
  vertical-align: top;
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  -webkit-transition: opacity .8s ease, -webkit-transform 1.4s ease;
  transition: opacity .8s ease, -webkit-transform 1.4s ease;
  transition: opacity .8s ease, transform 1.4s ease;
  transition: opacity .8s ease, transform 1.4s ease, -webkit-transform 1.4s ease;
  overflow-x: scroll;
}

.app{
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#meDiv{
  padding-top: 20%;
  display: flex;
  flex-direction: column;
  font-family: monospace;
  color: whitesmoke;
  font-size: x-large;
  text-align: center;
}

#meNote{
  font-size: x-small;
}
#emailInfo{
  font-size: small;
}

.contentArea{
    display: flex;
    flex-direction: column;
}

.card {
    min-width: 150px;
    max-width: 250px;
    min-height: 100px;
    max-height: 150px;
    background-color: whitesmoke;
    border-radius: 5%;
    margin: 0 auto;
}
